
































import { defineComponent, ref } from "@vue/composition-api";

export default defineComponent({
  name: "OrderHistory",

  props: {
    items: Array
  },

  setup() {
    const active = ref(false);
    const selected = ref(null);

    function formatOrder(num: number) {
      return "#" + num;
    }
    return {
      active,
      formatOrder,
      selected
    };
  }
});
